import axios from "axios";
import  './toster.css'
import React, { useEffect, useState } from "react";
import "./signup.css";
import { Link, Redirect } from "react-router-dom";
import { useHistory } from "react-router-dom";
import {
  ACCESS_TOKEN,
  BASE_URL,
  GOOGLE_AUTH_URL,
  normalLogin,
} from "../Apiurl";

export default function SignUp() {
  const [firstName, setFirstName] = useState("");
  const [lastName, setLastName] = useState("");
  const [mobileNumber, setMobileNumber] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [loading, setloading] = useState(false);
  const [state, setState] = useState("");
  const [userId, setUserId] = useState("");
  const [gender, setGender] = useState("");
  const [error, setError] = useState("");

  if(error){
    myFunction()
  }
  
    function myFunction() {
      var x = document.getElementById("snackbar");
      x.className = "show";
      setTimeout(function(){ x.className = x.className.replace("show", ""); }, 3000);
    }

  let history = useHistory();
  const handle = () => {
    // Email validation
    setloading(true);
    const emailPattern = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    if (!emailPattern.test(email)) {
      setError("Invalid email");
      setloading(false);
      return;
    }

    // Password validation
    const passwordPattern = /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)[a-zA-Z\d]{8,}$/;
    if (!passwordPattern.test(password)) {
      setError(
        "Password must be at least 8 characters long and contain at least one uppercase letter, one lowercase letter, and one number"
      );
      setloading(false);
      return;
    }

    // Mobile number validation
    const mobileNumberPattern = /^\d{10}$/;
    if (!mobileNumberPattern.test(mobileNumber)) {
      setError("Invalid mobile number");
      setloading(false);
      return;
    }

    var data = {
      alternativeMobile: mobileNumber,
      email: email,
      firstName: firstName,
      gender: gender,
      lastName: lastName,
      middleName: "",
      mobileNumber: mobileNumber,
      password: password,
      profileImage: "",
      proofNumber: "",
      proofPath: "",
      roleId: "efd97b9e-807f-1d5d-357b-f3540d271c10",
    };
    var postData = JSON.stringify(data);
    axios({
      method: "post",
      url: BASE_URL + "/auth-service/auth/sign-up",
      data: data,
    })
      .then((response) => {
        if (response.status == 201 || response.status == 200) {
          setloading(false);

          history.push("/login");
          localStorage.setItem("firstName", firstName);
        }
      })
      .catch((error) => {
        console.log(error.response);
        setError("enter valid email and phone number")
        setloading(false);
      });
  };

  return (
    <div>
      {" "}
      <div className="auth option2">
        <div className="auth_left">
          <div className="card">
            <div className="card-body">
              <div className="text-center">
                <Link className="header-brand" to="/">
                  <i className="fa fa-graduation-cap brand-logo"></i>
                </Link>
                <div className="card-title">Create new account</div>
                <button type="button" className="mr-1 btn btn-facebook">
                  <i className="fa fa-facebook mr-2"></i>Facebook
                </button>
                <a href={GOOGLE_AUTH_URL}>
                  <button type="button" className="btn btn-google">
                    <i className="fa fa-google mr-2"></i>Google
                  </button>{" "}
                </a>
                <h6 className="mt-3 mb-3">Or</h6>
              </div>
              <div className="samline">
                <div className="form-group ">
                  {/* <label className="form-label group">FirstName</label> */}
                  <input
                    type="text"
                    className="form-control conta"
                    placeholder="FirstName"
                    value={firstName}
                    onChange={(e) => setFirstName(e.target.value)}
                  />
                </div>
                <div className="form-group  ">
                  {/* <label className="form-label group">LastName</label> */}
                  <input
                    type="text"
                    className="form-control conta"
                    placeholder="LastName"
                    value={lastName}
                    onChange={(e) => setLastName(e.target.value)}
                  />
                </div>
              </div>
              <div className="form-group">
                {/* <label className="form-label">MobileNumber</label> */}
                <input
                  type="text"
                  className="form-control"
                  placeholder="Enter number"
                  value={mobileNumber}
                  onChange={(e) => setMobileNumber(e.target.value)}
                />
              </div>
              <div className="form-group">
                {/* <label className="form-label">Email address</label> */}
                <input
                  type="email"
                  className="form-control"
                  placeholder="Enter Email"
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                />
              </div>
              <div className="form-group">
                {/* <label className="form-label">Password</label> */}
                <input
                  type="password"
                  className="form-control"
                  placeholder="Password"
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                />
              </div>

              {/* <label className="form-label">gender</label>
					<input type="checkbox" className="form-control" placeholder="Password" value={gender} onChange={(e) => setGender(e.target.value)}/> */}
              <div className="form-group row">
                <label className="col-lg-3 col-form-label">Gender</label>
                <div className="col-lg-10">
                  <div className="form-check form-check-inline">
                    <input
                      className="form-check-input"
                      type="radio"
                      name="gender"
                      id="gender_male"
                      value="Male"
                      onChange={(e) => setGender(e.target.value)}
                    />
                    <label className="form-check-label" htmlFor="gender_male">
                      Male
                    </label>
                  </div>

                  <div className="form-check form-check-inline">
                    <input
                      className="form-check-input"
                      type="radio"
                      name="gender"
                      id="gender_female"
                      value="Female"
                      onChange={(e) => setGender(e.target.value)}
                    />
                    <label className="form-check-label" htmlFor="gender_female">
                      Female
                    </label>
                  </div>
                </div>
              </div>

              <div className="text-center">
                <button
                  type="submit"
                  className="btn btn-primary btn-block"
                  onClick={handle}
                >
                  {" "}
                  {loading ? (
                    <div
                      className="spinner-border text-drak"
                      role="status"
                      style={{ height: "20px", width: "20px" }}
                    >
                      <span className="sr-only">Loading...</span>
                    </div>
                  ) : (
                    "Create new account"
                  )}
                </button>
                <div id="snackbar">{error}</div>
                {/* {error && <div style={{ color: "red" }}>{error}</div>} */}
                <div className="text-muted mt-4">
                  Already have account? <Link to="/login">Sign in</Link>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
