import React from 'react';import './imageslider.css';import RightArrow from './rightside.svg'
export default function Ukslider() {
  return (
    <div>
    <div id="carouselExampleCaptions" className="carousel slide" data-bs-ride="false">
    <div className="carousel-indicators ">
      <button type="button" data-bs-target="#carouselExampleCaptions" data-bs-slide-to={0} className="active" aria-current="true" aria-label="Slide 1" />
      <button type="button" data-bs-target="#carouselExampleCaptions" data-bs-slide-to={1} aria-label="Slide 2" />
      <button type="button" data-bs-target="#carouselExampleCaptions" data-bs-slide-to={2} aria-label="Slide 3" />
    </div>
    <div className="carousel-inner">
      <div className="carousel-item active">
        <img src="https://cdn.britannica.com/85/13085-050-C2E88389/Corpus-Christi-College-University-of-Cambridge-England.jpg" className="d-block w-100" alt="..." />
        <div className="carousel-caption d-none d-md-block">
          <h5>University of Cambridge</h5>
          <p>England, United Kingdom</p>
        </div>
      </div>
      <div className="carousel-item">
      <img src="https://yourdreamschool.fr/app/uploads/2020/10/shutterstock_770971315-e1529418948592.jpg" className="d-block w-100" alt="..." />
        <div className="carousel-caption d-none d-md-block">
          <h5>Imperial college london</h5>
          <p>England, United Kingdom.</p>
        </div>
      </div>
      <div className="carousel-item">
      <img src="https://d3d00swyhr67nd.cloudfront.net/w800h800/EDI_UNI_location_image_1.jpg" className="d-block w-100" alt="..." />
        <div className="carousel-caption d-none d-md-block">
          <h5>The University of Edinburgh Art Collection</h5>
          <p>England, United Kingdom.</p>
        </div>
      </div>
    </div>
    <button className="carousel-control-prev" type="button" data-bs-target="#carouselExampleCaptions" data-bs-slide="prev">
      <span className="carousel-control-prev-icon" aria-hidden="true" />
      <span className="visually-hidden">Pre</span>
    </button>
    <button className="carousel-control-next" type="button" data-bs-target="#carouselExampleCaptions" data-bs-slide="next">
      <span className="carousel-control-next-icon" aria-hidden="true" />
      <span className="visually-hidden">Next</span>
    </button>
  </div>
  </div>
       
  )
}
