import axios from 'axios';
import { event } from 'jquery';
import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { BASE_URL } from '../Apiurl';

export default class Subforg extends Component {
 constructor(props) {
   super(props)
 
   this.state = {
	mobileNumber:"",
	data:'',
	mobileOtp:"",  
	email:"",
	error:'',
	mobileOtpSession:"",
	password:'',
   }
 }
    

 componentDidMount(){
	  

	const mobli=localStorage.getItem("mobileNumber");
	const mobileOtpSession=localStorage.getItem("mobileOtpSession");
	this.setState({mobileOtpSession:mobileOtpSession})
	this.setState({mobileNumber:mobli})
	// alert
	
	 
 }       

   

 handleChangemobileOtp = (e) => {
	this.setState({mobileOtp: e.target.value})
	
}
handleChangepassword = (e) => {
	this.setState({password: e.target.value})
	
}

	handle=()=>{     

		if(this.state.error){
			var x = document.getElementById("snackbar");
			x.className = "show";
			setTimeout(function(){ x.className = x.className.replace("show", ""); }, 3000);
		  }
		const { history } = this.props;
		var	data={
			"email":this.state.email,
			"mobileNumber": this.state.mobileNumber,
			"mobileOtp": this.state.mobileOtp,
			"mobileOtpSession": this.state.mobileOtpSession,
			"password": this.state.password,
		  }

		// alert(data.mobileOtpSession)
		   axios({
			method:'patch',
			url:BASE_URL+"/auth-service/user/forgot-password",
			data:data,
		   })
		   .then((res)=>res.data)
		   .then((res)=>{
               this.setState({data:res})
			//    alert(data)
			   history.push("/login")
		   })
		   .catch((error)=>this.setState({error:"invalid otp"}))
	}      

	render() {
		return (
			<div className="auth option2">
				<div className="auth_left">
					<div className="card">
						<div className="card-body">
							<div className="text-center">
								<Link className="header-brand" to="/">
									<i className="fa fa-graduation-cap brand-logo"></i>
								</Link>
								<div className="card-title">Forgot password</div>
							</div>
							<p className="text-muted">Enter your email or mobilenumber  address and your password will be reset and emailed to you.</p>
							<div className="form-group">
								<input type="number" className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" value={this.state.mobileNumber} placeholder="mobl"  />
							</div>

							<div className="form-group">

								<input type="number" className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Enter Otp" onChange={this.handleChangemobileOtp} />
							</div>

							<div className="form-group">
								
								<input type="password" className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Enter password" onChange={this.handleChangepassword} />
							</div>
							<div className="text-center">
								<div id='snackbar'>{this.state.error}</div>
								<button type="submit" className="btn btn-primary btn-block" onClick={this.handle}>Send me new password</button>
								<div className="text-muted mt-4">Forget it, <Link to="/login">Send me Back</Link> to the Sign in screen.</div>
							</div>
						</div>
					</div>
				</div>
			</div>
		);
	}
}
