import axios from 'axios';
import React, { useState } from 'react';
import { Link, useHistory } from 'react-router-dom';
import { ACCESS_TOKEN, BASE_URL, FACEBOOK_AUTH_URL,GOOGLE_AUTH_URL, Status } from '../Apiurl';

export default function Login() {    
	const [email, setEmail] = useState(""); 
	const [error, setError] = useState("");
	const [password, setPassword] = useState("");  
	const[token,settoken]=useState("");
	const [mobileNumber, setMobileNumber] = useState("");
	const [responseData, setResponseData] = useState(null);   	const [loading, setloading] = useState(false); 
	const [passwords, setPasswords] = useState("");
	const history=useHistory();    

   
	const handles=(event)=>{

		     event.preventDefault();
			 setloading(true);
			  // Email validation
			  const emailPattern = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
			  if (!emailPattern.test(email)) {
				setError("Invalid email");
				return;
			  }
		  
			  // Password validation
			  const passwordPattern = /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)[a-zA-Z\d]{8,}$/;
			  if (!passwordPattern.test(password)) {
				setError(
				  "Password must be at least 8 characters long and contain at least one uppercase letter, one lowercase letter, and one number"
				);
				return;
			  }
		  
			  // Mobile number validation
			//   const mobileNumberPattern = /^\d{10}$/;
			//   if (!mobileNumberPattern.test(mobileNumber)) {
			// 	setError("Invalid mobile number");
			// 	return;
			//   }
			  var data={
				"email":email,
				"password":password
				}



				axios({
					method:'post',
					url: BASE_URL+"/auth-service/auth/sign-in",
					data:data,
 
				}).then((response)=>{
					if(response.status==201 || response.status==200){
						
						alert("succ");   
						console.log(response.data);
						
						setResponseData(response.data);
						const fname=localStorage.getItem("firstName");
						alert(fname)
						const	tokens = response.data
									settoken({tokens:tokens})
									if(tokens.accessToken!=""){
										if(localStorage.getItem(Status)){
											localStorage.removeItem(Status)
										}
											console.log(response.data.roles[0].name);
											    localStorage.setItem(ACCESS_TOKEN, tokens.accessToken);
											   
									}
						history.push("/");
						setloading(false);
						
					}
				
			}).catch((error)=>{
                          console.log(error)
						  setloading(false);
				})
	}
  
  return (

	<div>
		<div className="auth option2">
	<div className="auth_left">
		<div className="card">
			<div className="card-body">
				<div className="text-center">
					<Link className="header-brand" to="/">
						<i className="fa fa-graduation-cap brand-logo"></i>
					</Link>
					<div className="card-title mt-3">Login to your account</div>
					<a href={FACEBOOK_AUTH_URL} >
					<button type="button" className="mr-1 btn btn-facebook"><i className="fa fa-facebook mr-2"></i>Facebook</button></a>
					<a  href={GOOGLE_AUTH_URL} >
					<button type="button" className="btn btn-google"><i className="fa fa-google mr-2"></i>Google</button>  </a>
					<h6 className="mt-3 mb-3">Or</h6>
				</div>
				<div className="form-group">
					<input type="email" className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Enter email" value={email}
					 onChange={(event) => setEmail(event.target.value) }/>
				</div>
				<div className="form-group">
					<label className="form-label"><Link to="forgotpassword" className="float-right small">I forgot password</Link></label>
					<input type="password" className="form-control" value={password} id="exampleInputPassword1" placeholder="Password"  name='password'
					  onChange={(event) => setPassword(event.target.value) }/>
				</div>
				{/* <div className="form-group">
					<label className="custom-control custom-checkbox">
						<input type="checkbox" className="custom-control-input" />
						<span className="custom-control-label">Remember me</span>
					</label>
				</div> */}
				<div className="text-center">
				<button
	 className="btn btn-primary btn-block"
	  onClick={handles}>
		{loading ?  <div className="spinner-border text-drak" role="status" style={{height:'20px',width:'20px'}}>
  <span className="sr-only">Loading...</span>
</div> : 	"Sign In" }
	  
	</button>{error && <div style={{color:"green"}}>{error}</div>}
			    
					<div className="text-muted mt-4">Don't have account yet? <Link to="/signup">Sign Up</Link></div>
				</div>
			</div>
		</div>
	</div>

</div></div>
  )
}
