import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { Sparklines, SparklinesBars } from 'react-sparklines';
import './tab.css'
import { TabContent, TabPane, Nav, NavLink, NavItem } from "reactstrap";
import classnames from 'classnames';

export default class Tab extends Component {
	constructor(props) {
		super(props);
		this.state = {
			activeTab: 1
		};
	}
	render() {
		const { activeTab } = this.state
		return (
			<>
				<div className="section-body">
					<div className="container-fluid">
						<div className="d-flex justify-content-between align-items-center ">
							
						</div>
					</div>
				</div>
				<div className="section-body mt-4">
					<div className="container-fluid">
						<div className="row clearfix">
							<div className="col-lg-12">
								<div className="card">
									<div className="card-body card1">
										<div className="d-md-flex justify-content-between">
											{/* <ul className="nav nav-pills b-none">
												<li className="nav-item"><a className="nav-link active" data-toggle="tab" href="#Invoice-list">Invoice List</a></li>
												<li className="nav-item"><a className="nav-link" data-toggle="tab" href="#Invoice-detail">Invoice Detail</a></li>
											</ul> */}
											<Nav className="nav-pills b-none">
												<NavItem>
													<NavLink
														className={classnames({ active: activeTab === 1 })}
														onClick={() => this.setState({ activeTab: 1 })}
													>
														Jan 2022
                   				</NavLink>
												</NavItem>
												<NavItem>
													<NavLink
														className={classnames({ active: activeTab === 2 })}
														onClick={() => this.setState({ activeTab: 2 })}
													>
														Dec 2022
                   				</NavLink>
												</NavItem>
											</Nav>
										
										</div>
										{/* <div className="input-group mt-2">
											<input type="text" className="form-control search" placeholder="Search..." />
										</div> */}
									</div>
								</div>
								<TabContent activeTab={activeTab}>
									<TabPane tabId={1} className={classnames(['fade show'])}>
										<div className='box'>
                                             <ul className='ulist'><li className='list'>Assiniboine Community College</li>
                                             
                                             <li className='list'>TWU</li>
                                             <li className='list'>Cleveland State University</li>
                                             <li className='list'>Saint Leo University</li>
                                             <li className='list'>MSVU</li>
                                             <li className='list'>George Mason University</li><li className='list'>ACapilano University</li></ul>
                                             
      
                                        </div>
										{/* </div> */}
									</TabPane>
									<TabPane tabId={2} className={classnames(['fade show'])}>
                                    <div className='box'>
                                             <ul className='ulist'><li className='list'>Assiniboine  College</li>
                                             
                                             <li className='list'>Loyalist College In Toronto</li>
                                             <li className='list'>Cegep Marie Victorin</li><li className='list'>Georgian College</li>
                                             {/* </ul> */}
                                             <li className='list'>Cleveland State University</li>
                                             <li className='list'>Saint Leo University</li>
                                             <li className='list'>MSVU</li></ul>
                                             
      
                                        </div>
									</TabPane>
								</TabContent>
							</div>
						</div>
					</div>
				</div>
			</>
		);
	}
}
