import axios from 'axios';
import { event } from 'jquery';
import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { BASE_URL } from '../Apiurl';

export default class ForgotPassword extends Component {
 constructor(props) {
   super(props)
 
   this.state = {
	mobileNumber:"",
	data:'',
	mobileOtpSession:'',
	error:''
	

   }
 }
  

 
 handleChangemo = (e) => {
	this.setState({mobileNumber: e.target.value})
	
}          

	handle=()=>{
		const { history } = this.props;
		const { error } = this.state;

		if(error){
			var x = document.getElementById("snackbar");
			x.className = "show";
			setTimeout(function(){ x.className = x.className.replace("show", ""); }, 3000);
		  }
	var	data={
		"mobileNumber": this.state.mobileNumber,
	  }

	  localStorage.setItem("mobileNumber" , this.state.mobileNumber); 
		// alert(data.mobileNumber)
		   axios({
			method:'post',
			url:BASE_URL+"/auth-service/user/send-mobile-otp",
			data:data,
		   })
		   .then((res)=>res.data)
		   .then((res)=>{
			console.log(res.mobileOtpSession);
	        this.setState({mobileOtpSession:res.mobileOtpSession})
			localStorage.setItem("mobileOtpSession",this.state.mobileOtpSession)
               this.setState({data:res})
			//    alert(data)
			   history.push("/subfor")

		   })
		   .catch((error)=>this.setState({error:"invalid mobile number"}))
		   
	}        

	render() {
		   const {error}=this.state;
		return (
			<div className="auth option2">
				<div className="auth_left">
					<div className="card">
						<div className="card-body">
							<div className="text-center">
								<Link className="header-brand" to="/">
									<i className="fa fa-graduation-cap brand-logo"></i>
								</Link>
								<div className="card-title">Forgot password</div>
							</div>
							<p className="text-muted">Enter your moblie number and your password will be reset and  to you.</p>
							<div className="form-group">
								<label className="form-label" for="exampleInputEmail1">Moblie Number</label>
								<input type="number" className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Enter number" onChange={this.handleChangemo} />
							</div>
							<div className="text-center">
							<div id="snackbar">{error}</div>
								<button type="submit" className="btn btn-primary btn-block" onClick={this.handle}>Send me new password</button>
								<div className="text-muted mt-4">Forget it, <Link to="/login">Send me Back</Link> to the Sign in screen.</div>
							</div>
						</div>
					</div>
				</div>
			</div>
		);
	}
}
