import React from 'react'
import './addnewtemp.css'
export default function AddnewTemp() {
  return (
    <div>
        <div className='maintem'>
               
               <h3>Edit Template</h3>

               <label className='temlabel'>Template Name</label>
               <input type='text' className='teminput' placeholder=' Enter Name' />
               <label className='temlabel'>Content</label>
               <textarea cols={5} placeholder="Enter the text" className='te'></textarea>  
              
               <input type='checkbox'   className='checkbox'  />
               <label className='for'>Statas</label>
               <button className='btn btn-primary met'>Add Template</button>
               <span style={{marginTop:'2rem' ,marginBottom:'2rem'}}></span>
        </div>
    </div>
  )
}
