import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { Sparklines, SparklinesBars } from 'react-sparklines';
import './tab.css';
import {UKimage} from './ukimage.png';
import './destitab.css'
import { TabContent, TabPane, Nav, NavLink, NavItem } from "reactstrap";
import classnames from 'classnames';
import Imageslider from './slider/Imageslider';
import Ukslider from './slider/Ukslider';
import Can from './slider/Can';
import Australia from './slider/Australia';
import Usa from './slider/Usa';

export default class DestiTab extends Component {
	constructor(props) {
		super(props);
		this.state = {
			activeTab: 1
		};
	}
	render() {
		const { activeTab } = this.state
		return (
			<>
				<div className="section-body">
					<div className="container-fluid">
						<div className="d-flex justify-content-between align-items-center ">
							
						</div>
					</div>
				</div>
				<div className="section-body mt-4">
					<div className="container-fluid">
						<div className="row clearfix">
							<div className="col-lg-12">
								<div className="card">
									<div className="card-body card1">
										<div className="d-md-flex justify-content-between">
											{/* <ul className="nav nav-pills b-none">
												<li className="nav-item"><a className="nav-link active" data-toggle="tab" href="#Invoice-list">Invoice List</a></li>
												<li className="nav-item"><a className="nav-link" data-toggle="tab" href="#Invoice-detail">Invoice Detail</a></li>
											</ul> */}
											<Nav className="nav-pills b-none n-nnn">
												<NavItem className="n-nnn">
													<NavLink
														className={classnames({ active: activeTab === 1 })}
														onClick={() => this.setState({ activeTab: 1 })}
													>
													<img src='https://upload.wikimedia.org/wikipedia/en/thumb/a/ae/Flag_of_the_United_Kingdom.svg/1280px-Flag_of_the_United_Kingdom.svg.png' className="ukimage" style={{height:'1rem',width:'1.5rem',objectFit:'cover'}}/>	UK
                   				</NavLink>
												</NavItem>
												<NavItem className="n-nnn">
													<NavLink
														className={classnames({ active: activeTab === 2 })}
														onClick={() => this.setState({ activeTab: 2 })}
													>
													<img src='https://upload.wikimedia.org/wikipedia/en/thumb/a/a4/Flag_of_the_United_States.svg/1280px-Flag_of_the_United_States.svg.png' className="ukimage" style={{height:'1rem',width:'1.5rem',objectFit:'cover'}}/>	USA
                   				</NavLink>
												</NavItem>
                                                <NavItem className="n-nnn">
													<NavLink
														className={classnames({ active: activeTab === 3 })}
														onClick={() => this.setState({ activeTab: 3 })}
													>
													<img src='https://cdn.britannica.com/68/7068-004-7848FEB4/Flag-Canada.jpg' className="ukimage" style={{height:'1rem',width:'1.5rem',objectFit:'cover'}}/> Canada
                   				</NavLink>
												</NavItem>

                                                <NavItem>
													<NavLink
														className={classnames({ active: activeTab === 4 })}
														onClick={() => this.setState({ activeTab:4  })}
													>
													<img src='https://cdn.britannica.com/78/6078-004-77AF7322/Flag-Australia.jpg' className="ukimage" style={{height:'1rem',width:'1.5rem',objectFit:'cover'}}/> Australia
                   				</NavLink>
												</NavItem>
											</Nav>
										
										</div>
										{/* <div className="input-group mt-2">
											<input type="text" className="form-control search" placeholder="Search..." />
										</div> */}
									</div>
								</div>
								<TabContent activeTab={activeTab}>
									<TabPane tabId={1} className={classnames(['fade show'])}>
										<div className='box'>
                                           <Ukslider />
      
                                        </div>
										{/* </div> */}
									</TabPane>
									<TabPane tabId={2} className={classnames(['fade show'])}>
                                    <div className='box'>
                                          <Usa />
                                             
      
                                        </div>   
									</TabPane>

                                    <TabPane tabId={3} className={classnames(['fade show'])}>
										<div className='box'>
                                           <Can />
      
                                        </div>
										{/* </div> */}
									</TabPane>
                                    <TabPane tabId={4} className={classnames(['fade show'])}>
										<div className='box'>
                                           <Australia />
      
                                        </div>
										{/* </div> */}
									</TabPane>
								</TabContent>
							</div>
						</div>
					</div>
				</div>
			</>
		);
	}
}
