import axios from "axios";
import React, { useState } from "react";


import './toster.css'
import { Link, useHistory } from "react-router-dom";
import {
  ACCESS_TOKEN,
  BASE_URL,
  FACEBOOK_AUTH_URL,
  GOOGLE_AUTH_URL,
  Status,
} from "../Apiurl";

export default function Log() {
  const [email, setEmail] = useState("");
  const [error, setError] = useState("");
  const [password, setPassword] = useState("");
  const [token, settoken] = useState("");
  const [mobileNumber, setMobileNumber] = useState("");
  const [responseData, setResponseData] = useState(null);
  const [loading, setloading] = useState(false);
  const [passwords, setPasswords] = useState("");
  const history = useHistory();

if(error){
  myFunction()
}

  function myFunction() {
    var x = document.getElementById("snackbar");
    x.className = "show";
    setTimeout(function(){ x.className = x.className.replace("show", ""); }, 3000);
  }

  const handles = (event) => {
    event.preventDefault();
    setloading(true);

    const regexs =
      /^(([^<>()[\]\.,;:\s@\"]+(\.[^<>()[\]\.,;:\s@\"]+)*)|(\".+\"))@(([^<>()[\]\.,;:\s@\"]+\.)+[^<>()[\]\.,;:\s@\"]{2,})$/i;
    if (regexs.test(email) === true) {
      if (!email || regexs.test(email) === false) {
        setError("Email is not valid");
        setloading({ loading });
        return false;
      } else {
        var data = {
          email:email,
          password:password,
        };
      
      }
    } else {
      if (!email || email.length < 10) {
        setError("Please enter valid number");
        setloading(false);
        return false;
        
      } else {
        var data = {
          mobileNumber: email,
          password: password,
        };
        
      }
    }

    axios({
      method: "post",
      url: BASE_URL + "/auth-service/auth/sign-in",
      data: data,
    })
      .then((response) => {
        setError("login sucess");
        if (response.status == 201 || response.status == 200) {

        console.log(response.data);
        

          setResponseData(response.data);
          const fname = localStorage.getItem("firstName");
          
          const tokens = response.data.accessToken;
          settoken({tokens});

           localStorage.setItem("accessToken",tokens)
          if (tokens.accessToken != "") {

            // alert("kjfv")
            history.push("/");
          setloading(false);
        
        }else{
            history.push("/login"); 
            setloading(false);
        }
        
          
        }
      })
      .catch((error) => {
        // alert(error);   
        
        setloading(false);setError("login credentials invalid");
      });
  };

  return (
    <div>
      <div className="auth option2">
        <div className="auth_left">
          <div className="card">
            <div className="card-body">
              <div className="text-center">
                <Link className="header-brand" to="/">
                  <i className="fa fa-graduation-cap brand-logo"></i>
                </Link>
                <div className="card-title mt-3">Login to your account</div>
                <a href={FACEBOOK_AUTH_URL}>
                  <button type="button" className="mr-1 btn btn-facebook">
                    <i className="fa fa-facebook mr-2"></i>Facebook
                  </button>
                </a>
                <a href={GOOGLE_AUTH_URL}>
                  <button type="button" className="btn btn-google">
                    <i className="fa fa-google mr-2"></i>Google
                  </button>{" "}
                </a>
                <h6 className="mt-3 mb-3">Or</h6>
              </div>
              <div className="form-group">
                <input
                  type="email"
                  className="form-control"
                  id="exampleInputEmail1"
                  aria-describedby="emailHelp"
                  placeholder="Enter email"
                  value={email}
                  onChange={(event) => setEmail(event.target.value)}
                />
              </div>
              <div className="form-group">
                <label className="form-label">
                  <Link to="forgotpassword" className="float-right small">
                    I forgot password
                  </Link>
                </label>
                <input
                  type="password"
                  className="form-control"
                  value={password}
                  id="exampleInputPassword1"
                  placeholder="Password"
                  name="password"
                  onChange={(event) => setPassword(event.target.value)}
                />
              </div>
              {/* <div className="form-group">
					<label className="custom-control custom-checkbox">
						<input type="checkbox" className="custom-control-input" />
						<span className="custom-control-label">Remember me</span>
					</label>
				</div> */}
              <div className="text-center">
                <button className="btn btn-primary btn-block" onClick={handles}>
                  {loading ? (
                    <div
                      className="spinner-border text-drak"
                      role="status"
                      style={{ height: "20px", width: "20px" }}
                    >
                      <span className="sr-only">Loading...</span>
                    </div>
                  ) : (
                    "Sign In"
                  )}
                </button>
                {/* <div id="snackbar">Some text some message..</div> */}
                <div id="snackbar">{error}</div>
                {/* {error && <div id='snackbar' style={{ color: "red" }}>{error}</div>} */}

                <div className="text-muted mt-4">
                  Don't have account yet? <Link to="/signup">Sign Up</Link>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
